import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { LoginService } from './login.service';
import { DialogueComponent } from '../shared/commmon/dialogue/dialogue.component';
import { MatDialog } from '@angular/material';
import { DialogueService } from '../shared/commmon/dialogue/dialogue.service';

@Component({
    selector: 'app-login',
    templateUrl: './login.component.html',
    styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
    UserName = '';
    PassWord = '';

    constructor(private router: Router, private service: LoginService, public dialog: MatDialog, private popup: DialogueService) {}

    ngOnInit() {}

    onLogin() {
        const body = {
            UserName: this.UserName,
            PassWord: this.PassWord
        };
        this.openDialog();
        this.popup.SetData({Message: 'Hang on', Status: '', Route: ''});
        window.localStorage.setItem('isLoggedin', 'true');
        if (this.UserName === 'admin') {
            this.router.navigate(['/ab/diet']);
        }
        if (this.UserName === 'customer') {
            this.router.navigate(['/cs']);
        }

    }
    // open dialogue
    openDialog(): void {
        const dialogRef = this.dialog.open(DialogueComponent, {
        width: '250px',
        disableClose: true
        });
    // fires when dialogue closed
        dialogRef.afterClosed().subscribe(result => {
        //
        });
    }
}
